import React, {Component} from 'react';
import './Todo.css'
import TodoList from "../components/todo-list/todo-list";
import Footer from "../components/todo-list/Footer";

export default class Todo extends Component {
    state = {
        listItems: [],
        currentState: 'All',
        isButtonHovered: false
    };

    addItemHandler = e => {
        if (e.target.value) {
            if (e.key === 'Enter') {
                let items = this.state.listItems;
                items.push({
                    id: items.length + 1,
                    isCompleted: false,
                    text: e.target.value
                });
                this.setState(items);
                e.target.value = ''
            }
        }
    };
    completeItemHandler = (id) => {
        if (!this.state.isButtonHovered) {
            let items = this.state.listItems;
            if (this.state.listItems.length !== 0) {
                if (!this.state.listItems[id - 1].isCompleted) {
                    items[id - 1].isCompleted = true;
                    this.setState(items);
                } else {
                    items[id - 1].isCompleted = false;
                    this.setState(items);
                }
            }
        }
    };
    deleteItemHandler = (id,e) => {
        let items = this.state.listItems;
        items[id - 1].isCompleted = true;
        items[id - 1].isDeleted = true;
        this.setState(items);
        this.setState({
            isButtonHovered:false
        })
    };
    currentStateSetter = state => {
        this.setState({
            currentState:state
        })
    };
    hoverButtonHandler = (bool) => {
        this.setState({
            isButtonHovered:bool
        })
    }

    render() {
        return (
            <div className="container">
                <div className="Todo">
                    <h1>TODO</h1>
                    <ul className="list-group">
                        <input
                            type="text"
                            className="list-group-item form-control form-control-lg"
                            placeholder="Что нужно сделать?"
                            onKeyPress={this.addItemHandler}
                            maxLength={50}
                        />
                        <TodoList
                            list={this.state.listItems}
                            onEnter={this.addItemHandler}
                            onComplete={this.completeItemHandler}
                            onDelete={this.deleteItemHandler}
                            currentState={this.state.currentState}
                            onHover={this.hoverButtonHandler}
                        />
                        <Footer
                            list={this.state.listItems}
                            onCurrentStateChange={this.currentStateSetter}
                        />
                    </ul>
                </div>
            </div>
        )
    }
}