import React from 'react'

const Footer = props => {
    const activeTasksCounter = () => {
        let sum = 0;
        let items = props.list;
        items.forEach((item) => {
            if (item.isCompleted === false) {
                sum++
            }
        });
        return sum
    };
    return (
        <li className="list-group-item">
            <div className="row">
                <div className="col-3">
                    <small>Осталось: {activeTasksCounter()}</small>
                </div>
                <div className="col-3 text-right">
                    <a href='#' onClick={() => props.onCurrentStateChange('All')}>Все</a>
                </div>
                <div className="col-3 text-right">
                    <a href='#' onClick={() => props.onCurrentStateChange('Active')}>Активные</a>
                </div>
                <div className="col-3 text-right">
                    <a href='#' onClick={() => props.onCurrentStateChange('Completed')}>Выполненные</a>
                </div>
            </div>
        </li>
    )
}

export default Footer;